<template>
  <div>
    <CardBox title="" height="100%" marginT="20">
      <div slot="extral">
        <el-button type="primary" size="mini" plain @click="handle_add"></el-button>
      </div>
      <template slot="body">
        <div class="table mgt-10">
          <el-table
            :data="tableData"
            border
            ref="selection"
            style="width: 100%"
            align="center">
            <el-table-column prop="host_ip" align="center" label="ip"></el-table-column>
            <el-table-column prop="exec_env" align="center" label=""></el-table-column>
            <el-table-column prop="host_user" align="center" label=""></el-table-column>
            <el-table-column prop="host_pwd" align="center" label="" :show-overflow-tooltip="true"></el-table-column>
            <el-table-column prop="host_path" align="center" label=""></el-table-column>
            <el-table-column prop="is_cur_env" align="center" label=""></el-table-column>
            <el-table-column label="" width="190">
              <template slot-scope="scope">
                <el-button
                  type="text"
                  size="mini"
                  icon="el-icon-delete"
                  style="color: red"
                  @click="handle_delete(scope.row)">
                  
                </el-button>
                <el-button
                  type="text"
                  size="mini"
                  icon="el-icon-edit"
                  @click="handle_edit(scope.row)">
                  
                </el-button>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </template>
    </CardBox>

    <Dialog v-bind:visible="edit_flag" width="600" @closeDialog="close_edit" :title="title+''">
      <div slot="con">
        <el-form :model="form" :rules="rules" ref="form" label-width="100px" style="margin-left: 100px" size="mini">
          <el-form-item label="ip" prop="host_ip">
            <el-input v-model="form.host_ip" style="width: 50%" size="mini"></el-input>
          </el-form-item>
          <el-form-item label="" prop="host_user">
            <el-input v-model="form.host_user" style="width: 50%"></el-input>
          </el-form-item>
          <el-form-item label="" prop="host_pwd">
            <el-input type="password" v-model="form.host_pwd" style="width: 50%"></el-input>
            <el-button type="primary" size="mini" @click="test_connect" :loading="loading" :disabled="loading">{{execute_button_name}}</el-button>
          </el-form-item>
          <el-form-item label="" prop="check_host_pwd">
            <el-input type="password" v-model="form.check_host_pwd" style="width: 50%"></el-input>
          </el-form-item>
          <el-form-item label="" prop="exec_env">
            <el-select v-model="form.exec_env" placeholder="">
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="host_path">
            <el-input v-model="form.host_path" style="width: 50%"></el-input>
          </el-form-item>
          <el-form-item label="" prop="is_cur_env">
            <el-select v-model="form.is_cur_env" placeholder="">
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
          </el-form-item>
        </el-form>
      </div>
      <div slot="footer">
        <div class="dialog__footer">
          <el-button type="primary" plain @click="handle_reset"></el-button>
          <el-button type="primary" @click="handle_submit('form')"></el-button>
        </div>
      </div>
    </Dialog>
  </div>
</template>

<script>
  import {getCookie} from "../../../service/cookie";
  import api from "../../../service/api";

  export default {
    components: {
      CardBox: () => import('@/components/common/CardBox'),
      Dialog: () => import('@/components/common/Dialog')
    },
    data() {
      let check_host_pwd = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''));
        } else if (value !== this.form.host_pwd) {
          callback(new Error('!'));
        } else {
          callback();
        }
      }
      return {
        loading: false,
        execute_button_name: '',
        host_id: '',
        tableData: [],
        title: '',
        edit_flag: false,
        form: {
          host_ip: '',
          exec_env: '',
          host_user: '',
          host_pwd: '',
          host_path: '',
          check_host_pwd: '',
          is_cur_env: ''
        },
        rules: {
          host_ip: [{required: true, message: 'ip', trigger: 'change'}],
          host_user: [{required: true, message: '', trigger: 'change'}],
          exec_env: [{required: true, message: '', trigger: 'change'}],
          host_path: [{required: true, message: '', trigger: 'change'}],
          is_cur_env: [{required: true, message: '', trigger: 'change'}],
          host_pwd: [{required: true, message: '', trigger: 'change'}],
          check_host_pwd: [
            {required: true, message: '', trigger: 'change'},
            {validator: check_host_pwd, trigger: 'blur'}
          ],
        },
      }
    },
    methods: {
      // 
      test_connect() {
        this.loading = true
        this.execute_button_name = ''
        let data = {
          host_ip: this.form.host_ip,
          host_user: this.form.host_user,
          host_pwd: this.form.host_pwd
        }
        if (! this.form.host_ip) {
          this.$message.warning('ip')
          this.loading = false
          this.execute_button_name = ''
          return false
        }
        if (! this.form.host_user) {
          this.$message.warning('')
          this.loading = false
          this.execute_button_name = ''
          return false
        }
        if (! this.form.host_pwd) {
          this.$message.warning('')
          this.loading = false
          this.execute_button_name = ''
          return false
        }
        api.config_manage_api.test_connect(data).then(response => {
          if (response.status === -2) {
            this.$message.warning('SSH!')
            this.execute_button_name = ''
          } else if (response.status === 0) {
            this.$message.success('')
            this.execute_button_name = ''
          } else {
            this.$message.error('')
          }
          this.loading = false
        })
      },
      // 
      close_edit() {
        this.edit_flag = false
        this.execute_button_name = ''
      },
      //
      handle_search() {
        let data = {
          user_id: getCookie('user_id')
        };
        api.config_manage_api.query_host_data(data).then(response => {
          if (response.status === 0) {
            this.tableData = response.info
          } else {
            this.$message.error('')
          }
        });
      },
      //
      handle_delete(rows) {
        this.$confirm('', '', {type:"warning"}).then(_ => {
          let data = {
            host_id: rows.host_id,
            user_id: getCookie('user_id')
          }
          api.config_manage_api.delete_host_data(data).then(response => {
            if (response.status === 0) {
              this.$message.success('')
              this.handle_search()
            } else {
              this.$message.error('')
            }
          })
        }).catch(_ => {})
      },
      // 
      handle_edit(row) {
        this.host_id = row.host_id
        this.form = {
          host_ip: row.host_ip,
          exec_env: row.exec_env,
          host_user: row.host_user,
          host_pwd: row.host_pwd,
          check_host_pwd: row.host_pwd,
          is_cur_env: row.is_cur_env,
          host_path: row.host_path
        }
        this.title = ''
        this.edit_flag = true
      },
      // 
      handle_add() {
        this.host_id = ''
        this.form = {
          host_ip: '',
          exec_env: '',
          host_user: '',
          host_pwd: '',
          check_host_pwd: '',
          is_cur_env: ''
        }
        this.title = ''
        this.edit_flag = true
      },
      // 
      handle_submit(form) {
        this.$refs[form].validate((valid) => {
          if (valid) {
            let data = {
              user_id: getCookie('user_id'),
              host_id: this.host_id,
              host_path: this.form.host_path,
              host_ip: this.form.host_ip,
              exec_env: this.form.exec_env,
              host_user: this.form.host_user,
              host_pwd: this.form.host_pwd,
              is_cur_env: this.form.is_cur_env
            }
            switch (this.title) {
              case "":
                api.config_manage_api.add_host_data(data).then(response => {
                  if (response.status === 0) {
                    this.$message.success('')
                    this.edit_flag = false
                    this.handle_search()
                  } else if (response.status === -2) {
                    this.$message.error(response.info)
                  } else {
                    this.$message.error('')
                  }
                });
                break;
              case "":
                api.config_manage_api.update_host_data(data).then(response => {
                  if (response.status === 0) {
                    this.$message.success('')
                    this.edit_flag = false
                    this.handle_search()
                  } else if (response.status === -2) {
                    this.$message.error(response.info)
                  } else {
                    this.$message.error('')
                  }
                });
                break;
            }
          }
        })
      },
      // 
      handle_reset(form) {
        this.form = {
          host_ip: '',
          exec_env: '',
          host_user: '',
          host_pwd: '',
          check_host_pwd: '',
          is_cur_env: ''
        }
      }
    },
    mounted() {
      this.handle_search()
    }
  }
</script>
<style lang="scss" scoped>
  .el-upload__tip {
    display: inline-block;
    margin-left: 8px;
    color: #666;
    font-size: 14px;
  }

  .icon_word {
    width: 10px;
    margin-right: 2px;

  }

</style>



