<template>
  <div>
    <CardBox title="" height="90px">
      <template slot="body">
        <el-form :inline="true" class="mgt-10">
          <el-row type="flex">
            <el-col :span="6">
              <el-form-item label="">
                <el-input
                  size="small"
                  clearable
                  v-model="search_input.offer_id"
                  placeholder="">
                </el-input>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item>
                <el-button
                  type="primary"
                  size="small"
                  plain
                  @click="reset_search"
                  style="background:none">
                </el-button>
                <el-button type="primary" size="small" @click="handle_search"></el-button>
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>
      </template>
    </CardBox>

    <CardBox title="" height="100%" marginT="20">
      <template slot="body">
        <div class="table_desc">
          <p>{{ require_info }}</p>
          <p>
            {{ require_desc }}
          </p>
        </div>
        <div class="table">
          <el-table
            :data="all_test_table_data"
            element-loading-text=""
            border
            ref="selection"
            style="width: 100%"
            align="center">
            <el-table-column prop="test_date" label="">
            </el-table-column>
            <el-table-column prop="pass_cont" label=""></el-table-column>
            <el-table-column prop="not_pass_cont" label=""></el-table-column>
            <el-table-column prop="session_id" align="center" label="session_id" v-if="false"></el-table-column>
            <el-table-column label="" width="150">
              <template slot-scope="scope">
                <el-button type="text" @click="cat_report(scope.row)"></el-button>
              </template>
            </el-table-column>
          </el-table>
          <div class="search_pagination tr">
            <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :current-page.sync="all_test_current_page"
              :page-size="all_test_page_size"
              :page-sizes="[5,10,15]"
              layout="sizes, prev, pager, next"
              :total="all_test_total_count">
            </el-pagination>
          </div>
        </div>
      </template>
    </CardBox>

    <Dialog
      :visible="seeReport"
      width="1025"
      @closeDialog="seeReport = false"
      title="">
      <div slot="con">
        <div class="table_desc">
          <p>
            {{ offer_name }}
          </p>
        </div>
        <div class="table">
          <el-table
            :data="report_table_data"
            border
            ref="selection"
            style="width: 100%"
            align="center">
            <el-table-column prop="case_name" label="">
              <template slot-scope="scope">
                <el-popover placement="bottom" width="300" trigger="hover">
                  <el-table :data="scope.row.test_point" class="popber_table" size="small">
                    <el-table-column
                      property="point_name"
                      label=""
                      width="190"
                    ></el-table-column>
                    <el-table-column
                      property="diff"
                      label=""
                    ></el-table-column>
                  </el-table>
                  <p slot="reference">{{ offer_name }}</p>
                </el-popover>
              </template>
            </el-table-column>
            <el-table-column prop="test_result" label="">
              <template slot-scope="scope">
                <p class="res_status">
                  <i class="el-icon-circle-check" style="color: lime" v-if="scope.row.test_result===''"></i>
                  <i class="el-icon-circle-close" style="color: red" v-if="scope.row.test_result===''"></i>
                  {{scope.row.test_result}}
                </p>
              </template>
            </el-table-column>
            <el-table-column label="" width="150">
              <template slot-scope="scope">
                <el-button type="text" @click="cat_detail_result(scope.row)"></el-button>
              </template>
            </el-table-column>
          </el-table>
          <div class="search_pagination tr">
            <el-pagination
              @size-change="handleSizeChange2"
              @current-change="handleCurrentChange2"
              :current-page.sync="report_current_page"
              :page-size="report_page_size"
              :page-sizes="[5,10,15]"
              layout="sizes, prev, pager, next"
              :total="report_total_count">
            </el-pagination>
          </div>
        </div>
        <p class="conclusion">
          
          <span style="color: yellowgreen">{{ test_conclusion.report_conclusion }}</span><br><br>
        </p>
        <div class="text_callback">
          <div>
            
            <el-radio-group v-model="radio">
              <el-radio :label="1"></el-radio>
              <el-radio :label="2"></el-radio>
            </el-radio-group>
          </div>
          <el-button
            type="primary"
            size="mini"
            style="background:#1677FF"
            @click="check_result_flag = true">
          </el-button>
        </div>
      </div>
      <div slot="footer">
        <div class="dialog__footer">
          <el-button type="primary" class="import-btn" :loading="rc_loading" icon="el-icon-folder-add" @click="exportReport"></el-button>
        </div>
      </div>
    </Dialog>

    <Dialog
      :visible="check_result_flag"
      width="250"
      @closeDialog="check_result_flag = false"
      message-title=""
      :modal="false"
      top="30vh">
      <div slot="con">
        
      </div>
      <div slot="footer">
        <div class="dialog__footer">
          <el-button
            type="primary"
            plain
            size="small"
            style="padding:8px 30px"
            @click="check_result_flag = false">
          </el-button>
          <el-button
            type="primary"
            size="small"
            style="padding:8px 30px"
            @click="author_check">
          </el-button>
        </div>
      </div>
    </Dialog>

    <Dialog
      :visible="case_detail_flag"
      width="911"
      @closeDialog="case_detail_flag = false"
      title="">
      <div slot="con">
        <div class="table">
          <el-table
            :data="case_detail_table_data"
            border
            @row-click="rowClick"
            ref="PrcTable"
            style="width: 100%"
            align="center">
            <el-table-column type="expand">
              <template slot-scope="scope">
                <el-table :data="scope.row.test_point">
                  <el-table-column align="center" :label=scope.row.point_type>
                    <el-table-column property="point_name" label=""></el-table-column>
                    <el-table-column property="expect_info" label=""></el-table-column>
                    <el-table-column property="result_info" label=""></el-table-column>
                    <el-table-column property="diff" label="">
                      <template slot-scope="scope">
                        <span v-if="scope.row.diff !== ''" style="color: red">{{scope.row.diff}}</span>
                        <span v-else style="color: black">{{scope.row.diff}}</span>
                      </template>
                    </el-table-column>
                  </el-table-column>
                </el-table>
              </template>
            </el-table-column>
            <el-table-column prop="case_name" label="">
            </el-table-column>
            <el-table-column prop="msisdn" label=""></el-table-column>
            <el-table-column prop="test_result" label="">
              <template slot-scope="scope">
                <span v-if="scope.row.test_result !== ''" style="color: red">{{scope.row.test_result}}</span>
                <span v-else style="color: black">{{scope.row.test_result}}</span>
              </template>
            </el-table-column>
            <el-table-column label="" width="300">
              <template slot-scope="scope">
                <el-button type="text" @click="cat_list_detail(scope.row, 0)"></el-button>
                <el-button type="text" @click="cat_list_detail(scope.row, 1)"></el-button>
              </template>
            </el-table-column>
          </el-table>
        </div>
        <div style="padding-bottom:30px">
          <p class="conclusion" style="margin:48px 0 0 0">
            {{ test_conclusion.case_conclusion }}
          </p>
        </div>
      </div>
    </Dialog>

    <Dialog :visible="cdr_flag"
            width="911"
            @closeDialog="cdr_flag = false"
            :title="''">
      <div slot="con">
        <div class="table pdb-30">
            <el-table
              :data="list_table_data"
              border
              ref="selection"
              style="width: 100%">
              <el-table-column v-for="(item, index) in dynamic_column" :key="index" :label="item.chn_name"
                               align="center">
                <template slot-scope="scope">
                  {{ scope.row.field_info[index].value}}
                </template>
              </el-table-column>
            </el-table>
        </div>
      </div>
    </Dialog>

    <Dialog :visible="acc_flag"
            width="911"
            @closeDialog="acc_flag = false"
            :title="''">
      <div slot="con">
        <div class="table pdb-30">
            <el-table
              :data="list_table_data"
              border
              ref="selection"
              style="width: 100%">
              <el-table-column prop="duration"  label="" align="center"></el-table-column>
              <el-table-column prop="offerFee"  label="" align="center"></el-table-column>
              <el-table-column prop="offerOutFee"  label="()" align="center"></el-table-column>
              <el-table-column prop="totalFee"  label="" align="center"></el-table-column>
            </el-table>
        </div>
      </div>
    </Dialog>
  </div>
</template>

<script>
  import {getCookie} from "../../../service/cookie";
  import api from "../../../service/api";

  export default {
    components: {
      CardBox: () => import('@/components/common/CardBox'),
      Dialog: () => import('@/components/common/Dialog')
    },
    data() {
      return {
        instance_id: '',
        session_id: '',
        // 
        offer_id: '',
        // 
        require_info: '',
        // 
        require_desc: '',
        // 
        search_input: {
          offer_id: ''
        },
        // 
        all_test_table_data: [],
        all_test_current_page: 1,
        all_test_page_size: 10,
        all_test_total_count: 0,
        // 
        seeReport: false,
        report_table_data: [],
        report_current_page: 1,
        report_page_size: 10,
        report_total_count: 0,
        offer_name: '',
        // 
        radio: 1,
        check_result_flag: false,
        // 
        case_detail_flag: false,
        case_detail_table_data: [],
        case_detail_desc_info: '',
        // 
        cdr_flag: false,
        acc_flag: false,
        list_table_data: [],
        // 
        test_conclusion: {
          report_conclusion: '',
          case_conclusion: ''
        },
        // 
        rc_loading: false,

      }
    },
    methods: {
      // 
      handle_search() {
        if (!this.search_input.offer_id) {
          this.$message.warning('id')
        }
        let data = {
          pageSize: this.all_test_page_size,
          page: this.all_test_current_page,
          offer_id: this.search_input.offer_id,
          user_id: getCookie('user_id')
        }
        api.config_manage_api.all_test_result(data).then(response => {
          if (response.status === 0) {
            this.require_info = ""
            this.require_desc = response.info[0].require_name
            this.offer_name = response.info[0].offer_name
            this.all_test_table_data = response.info
            this.all_test_total_count = response.total_count
          } else {
            this.$message.error('')
          }
        });

      },
      // 
      reset_search() {
        this.search_input.offer_id = this.offer_id
        this.handle_search()
      },
      // 
      handleSizeChange(size) {
        this.all_test_page_size = size
        this.handle_search()
      },
      handleCurrentChange(val) {
        this.all_test_current_page = val
        this.handle_search()
      },
      // 
      handleSizeChange2(size) {
        this.report_page_size = size
        this.get_report_result()
      },
      handleCurrentChange2(val) {
        this.report_current_page = val
        this.get_report_result()
      },
      // 
      cat_report(row) {
        this.session_id = row.session_id
        this.instance_id = row.instance_id
        this.seeReport = true
        this.get_report_result()
      },
      // 
      get_report_result() {
        let data = {
          instance_id: this.instance_id,
          pageSize: this.report_page_size,
          page: this.report_current_page,
        }
        api.config_manage_api.get_test_report(data).then(response => {
          if (response.status === 0) {
            this.offer_name = response.info.offer_name
            this.report_table_data = response.info.case_user_info
            this.report_total_count = response.info.total_count
            this.test_conclusion.report_conclusion = response.info.test_conclusion
          } else {
            this.$message.error('')
          }
        })
      },
      // 
      author_check() {
        let check_desc = ''
        this.radio === 1 ? check_desc = "" : ""
        let data = {
          offer_id: this.offer_id,
          session_id: this.session_id,
          check_desc: check_desc
        }
        api.config_manage_api.test_author_isfocus(data).then(response => {
          if (response.status === 0) {
            this.$message.success('')
          } else {
            this.$message.error('')
          }
          this.check_result_flag = false
        })
      },
      // 
      cat_detail_result(row) {
        this.case_detail_flag = true
        let data = {
          task_id: row.task_id,
          user_id: getCookie('user_id')
        }
        api.config_manage_api.get_test_report(data).then(response => {
          if (response.status === 0) {
            this.case_detail_table_data = response.info
            this.test_conclusion.case_conclusion = response.test_conclusion
          } else {
            this.$message.error('')
          }
        })
      },
      rowClick() {
        this.$refs.PrcTable.toggleRowExpansion();
      },

      cat_list_detail(row, flag) {
        let data = {
          task_id: row.task_id,
          msisdn: row.msisdn,
          flag: flag,
        }
        flag === 0 ? this.cdr_flag = true : this.acc_flag = true
        api.config_manage_api.get_list_fedx(data).then(response => {
          if (response.status === 0) {
            this.list_table_data = response.info
          } else {
            this.$message.error('')
          }
        })
      },
      // 
      exportReport () {
        this.rc_loading = true
        let data = {
          info: this.report_table_data,
          offer_name: this.offer_name,
          user_id:getCookie('user_id')
        }
        api.config_manage_api.create_report(data).then(res => {
          if (res.status === -4){
            alert('')
            this.$router.push('/')
          } else {
            this.rc_loading = false
            console.log('',res)
            window.open(res)
          }
        })
      },
    },
    computed: {
      dynamic_column: function () {
        if (this.list_table_data.length > 0) {
          return this.list_table_data[0].field_info
        } else {
          return []
        }
      }
    },
    mounted() {
      // console.log(this.$route)
      if (this.$route.query.hasOwnProperty('offer_id')) {
        this.offer_id = this.$route.query.offer_id
        this.search_input.offer_id = this.$route.query.offer_id
        this.handle_search()
      }
    }
  }
</script>
<style lang="scss" scoped>
  .table_desc {
    font-size: 16px;
    display: flex;
    padding: 12px 0;
    color: #333;

    p {
      margin-right: 80px;
    }
  }

  .res_status {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 16px;

    .pass {
      width: 22px;
      height: 22px;
      display: block;
      margin-right: 6px;
      background: url(~@/assets/images/pic/pass.png) no-repeat center center/100% 100%;
    }
  }

  .conclusion {
    color: #2e58f7;
    font-size: 14px;
    padding-left: 32px;
    position: relative;
    margin: 28px 0 40px 0;

    &::before {
      content: "";
      width: 24px;
      height: 24px;
      position: absolute;
      left: 0;
      top: 50%;
      display: block;
      background: url(~@/assets/images/pic/jielun.png) no-repeat center center/100% 100%;
      margin-top: -12px;
    }
  }

  .text_callback {
    padding: 5px 20px;
    background: rgba(102, 102, 102, 0.05);
    border-radius: 4px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  .upload_over {
    font-size: 16px;
    text-align: center;
    padding-bottom: 19px;

    img {
      display: block;
      margin: 0 auto;
      margin-bottom: 20px;
      width: 42px;
    }
  }

  .clear_text {
    color: #333;
    font-size: 14px;
    margin: 16px 0 8px 0;
  }

  .popber_shadow {
    padding: 12px 20px;
    background: #ffffff;
    border-radius: 4px;
    box-shadow: 0px 2px 8px 0px rgba(0, 0, 0, 0.10);
  }
</style>



