<template>
  <div>
    <CardBox title="" height="90px">
      <template slot="body">
        <el-form :inline="true" class="mgt-10 form_search">
          <el-form-item label="">
            <el-input placeholder="" clearable v-model="search_input.test_name" size="mini">
              <i slot="prefix" class="el-input__icon el-icon-search"></i>
            </el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input placeholder="" clearable v-model="search_input.offer_id" size="mini">
              <i slot="prefix" class="el-input__icon el-icon-search"></i>
            </el-input>
          </el-form-item>
          <el-form-item label="">
            <el-select size="small" v-model="search_input.test_status" placeholder="">
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" size="small" plain style="background:none" @click="reset_search"></el-button>
            <el-button type="primary" size="small" @click="handle_search"></el-button>
          </el-form-item>
        </el-form>
      </template>
    </CardBox>

    <CardBox title="" height="100%" marginT="20">
      <template slot="body">
        <div class="table mgt-10">
          <el-table
            :data="tableData"
            border
            ref="selection"
            style="width: 100%"
            align="center">
            <el-table-column prop="require_name" label=""></el-table-column>
            <el-table-column prop="offer_id" label=""></el-table-column>
            <el-table-column prop="test_status" label=""></el-table-column>
            <el-table-column prop="test_date" label=""></el-table-column>
            <el-table-column label="" width="190">
              <template slot-scope="scope">
                <el-button
                  type="text"
                  size="mini"
                  icon="el-icon-delete"
                  style="color: red"
                  @click="handle_delete(scope.$index, scope.row)">
                  
                </el-button>
                <el-button
                  type="text"
                  size="mini"
                  icon="el-icon-edit"
                  @click="handle_edit(scope.row)">
                  
                </el-button>
                <el-button type="text" size="mini" @click="look_result(scope.row)" style="color: green" icon="el-icon-view">
                  
                </el-button>
              </template>
            </el-table-column>
          </el-table>
          <div class="search_pagination tr">
            <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :current-page.sync="currentPage"
              :page-size="pageSize"
              :page-sizes="[5,10,15]"
              layout="sizes, prev, pager, next"
              :total="total_count">
            </el-pagination>
          </div>
        </div>
      </template>
    </CardBox>

    <Dialog v-bind:visible="visibleEdit" width="600" @closeDialog="close_dialog" :title="dailogTitleType+''">
      <div slot="con">
        <el-form :model="formEdit" label-width="140px" :rules="rules" ref="formEdit">
          <el-form-item label="" prop="require_name">
            <el-input
              placeholder=""
              v-model="formEdit.require_name"
              style="width:225px"
            ></el-input>
          </el-form-item>
          <el-form-item label="" prop="offer_id">
            <el-input
              placeholder=""
              v-model="formEdit.offer_id"
              style="width:225px"
            ></el-input>
          </el-form-item>
          <el-form-item label="" prop="offer_name">
            <el-input
              placeholder=""
              v-model="formEdit.offer_name"
              style="width:225px"
            ></el-input>
          </el-form-item>
          <el-form-item label="" prop="test_date">
            <el-date-picker
              v-model="formEdit.test_date"
              type="datetime"
              value-format="yyyy-MM-dd HH:mm:ss"
              placeholder=""
              style="width:225px">
            </el-date-picker>
          </el-form-item>
        </el-form>
      </div>
      <div slot="footer">
        <div class="dialog__footer">
          <el-button type="primary" plain @click="reset_form"></el-button>
          <el-button type="primary" @click="submit('formEdit')"></el-button>
        </div>
      </div>
    </Dialog>
  </div>
</template>

<script>
  import {getCookie} from "../../../service/cookie";
  import api from "../../../service/api";

  export default {
    components: {
      CardBox: () => import('@/components/common/CardBox'),
      Dialog: () => import('@/components/common/Dialog')
    },
    data() {
      return {
        tableData: [],
        currentPage: 1,
        pageSize: 10,
        total_count: 0,

        search_input: {
          test_name: '',
          offer_id: '',
          test_status: ''
        },
        options: [
          {
            value: '',
            label: ''
          },
          {
            value: '',
            label: ''
          }
        ],

        dailogTitleType: "",
        visibleEdit: false,

        rules: {
          require_name: [
            {required: true, message: '', trigger: 'blur'},
          ],
          offer_id: [
            {required: true, message: '', trigger: 'blur'},
          ],
          offer_name: [
            {required: true, message: '', trigger: 'blur'},
          ],
          test_date: [
            {required: true, message: '', trigger: 'blur'}
          ]
        },
        formEdit: {
          require_name: '',
          offer_id: '',
          offer_name: '',
          test_date: '',
          require_id: ''
        },
      }
    },
    methods: {
      handleSizeChange (size) {
        this.pageSize = size
        this.handle_search()
      },
      handleCurrentChange (val) {
        this.currentPage = val
        this.handle_search()
      },
      close_dialog () {
        this.visibleEdit = false
      },
      // 
      reset_search () {
        this.search_input = {
          test_name: '',
          offer_id: '',
          test_status: ''
        }
        this.handle_search()
      },
      // 
      handle_search () {
        let data = {
          page: this.currentPage,
          pageSize: this.pageSize,
          require_name: this.search_input.test_name,
          offer_id: this.search_input.offer_id,
          test_status: this.search_input.test_status,
          user_id: getCookie('user_id')
        }
        api.config_manage_api.check_test_require(data).then(res => {
          if (res.status === 0) {
            this.tableData = res.info
            this.total_count = res.total_count
          } else {
            this.$message.error('')
          }
        })
      },
      // 
      handle_delete (index, row) {
        this.$confirm('').then(_ => {
          let data = {
            require_id: row.require_id,
            user_id: getCookie('user_id')
          }
          api.config_manage_api.delete_test_require(data).then(response => {
            if (response.status === 0) {
              this.$message.success('')
              this.handle_search()
            }
            else {
              this.$message.error('')
            }
          });
        })
      },
      // 
      handle_edit (row) {
        this.visibleEdit = true
        this.dailogTitleType = ""
        // 
        this.formEdit.require_name = row.require_name
        this.formEdit.offer_id = row.offer_id
        this.formEdit.test_date = row.test_date
        this.formEdit.offer_name = row.offer_name
        this.formEdit.require_id = row.require_id
      },
      // 
      reset_form () {
        this.formEdit = {
          require_name: '',
          offer_id: '',
          offer_name: '',
          test_date: '',
        }
      },
      // 
      submit (form) {
        console.log(form)
        this.$refs[form].validate(valid => {
          if (valid) {
            let data = {
              require_id: this.formEdit.require_id,
              require_name: this.formEdit.require_name,
              offer_id: this.formEdit.offer_id,
              offer_name: this.formEdit.offer_name,
              test_date: this.formEdit.test_date
            }
            api.config_manage_api.add_test_require(data).then(res => {
              if (res.status === 0) {
                this.require_id = ''
                this.$message.success('')
                this.visibleEdit = false
                this.currentPage = 1
                this.handle_search()
              }
              else {
                this.$message.error('')
              }
            })
          }
        })
      },
      // 
      look_result (rows) {
        console.log(this.$router)
        this.$router.push({
          path: '/result/all_net_test_result',
          query: {
            offer_id: rows.offer_id
          }
        })
      }
    },
    mounted() {
      this.currentPage = 1
      this.handle_search()
    }
  }
</script>
<style lang="scss" scoped>
  .el-upload__tip {
    display: inline-block;
    margin-left: 8px;
    color: #666;
    font-size: 14px;
  }

  .icon_word {
    width: 10px;
    margin-right: 2px;

  }
</style>



