export default {
  request(url, data = {}, method = 'get') {
  return new Promise((resolve, reject) => {
    const options = {
      url,
      method
    };
    if (method.toLowerCase() === 'get') {
      options.params = data
    } else {
      options.data = data
      //console.log(data)
    }
    service(options)
      .then(res => {
        resolve(res.data)
      })
      .catch(error => {
        reject(error);
      })
  })
},
  get(url, params = {}) {
    return new Promise((resolve, reject) => {
      axios.get(baseURL + url, {params}).then(res => {
        resolve(res.data);
      }).catch(error => {
        reject(error);
      })
    })
  },
  post(url, params = {}) {
    return new Promise((resolve, reject) => {
      axios.post(baseURL + url, params).then(res => {
        resolve(res.data);
      }).catch(error => {
        reject(error);
        this.$message.error('')
      })
    })
  },
}





